import React, { useEffect, useImperativeHandle, useState } from "react";
import { DIETARY_PREFERENCES_DK } from "~/utils/constants";
import "react-autocomplete-input/dist/bundle.css";
import EditMenuModel from "./editMenuModel";
import CustomInput from "./customInput";

const EditMenuItem = React.forwardRef(
  (
    {
      station,
      stationId,
      editContent,
      existingTableData,
      indexValue,
      locationId,
      stationColor,
      bottomLine,
      preventDouble,
    },
    _ref
  ) => {
    const stationMenu = existingTableData?.some(
      (data) => data?.station_name?.id == station?.id
    )
      ? existingTableData?.filter(
          (data) => data?.station_name?.id == station?.id
        )[0]
      : {};
    const [selectedMenuItems, setSelectedMenuItems] = useState(
      existingTableData?.some((data) => data?.station_name?.id == station?.id)
        ? stationMenu?.recipe_id?.allergens_list
            ?.filter((e) => e.Allergens_id != null)
            .map((item) => item?.Allergens_id?.id) ?? []
        : []
    );
    const [selectedDietaryItems, setSelectedDietaryItems] = useState(
      existingTableData?.some((data) => data?.station_name?.id == station?.id)
        ? stationMenu?.recipe_id?.dietary_list
            ?.filter((e) => e.Dietary_id != null)
            .map((item) => item?.Dietary_id?.id) ?? []
        : []
    );
    const [isError, setIsError] = useState(false);
    const [menuModalIsOpen, setMenuIsOpen] = useState(false);
    const [menuInfo, setMenuInfo] = useState(
      existingTableData?.some((data) => data?.station_name?.id == station?.id)
        ? stationMenu?.recipe_id?.menu_info ?? ""
        : ""
    );
    const [translatedMenuInfo, setTranslatedMenuInfo] = useState(
      existingTableData?.some((data) => data?.station_name?.id == station?.id)
        ? stationMenu?.recipe_id?.menu_info_1 ?? ""
        : ""
    );
    const [totalCalorie, setTotalCalorie] = useState(
      existingTableData.some((data) => data.station_name?.id == station?.id)
        ? {
            calories:
              stationMenu?.recipe_id?.total_calories != null
                ? Number(stationMenu?.recipe_id?.total_calories)?.toFixed(2)
                : null,
            co2:
              stationMenu?.recipe_id?.total_calories != null
                ? Number(stationMenu?.recipe_id?.total_co2)?.toFixed(2)
                : null,
          }
        : {
            calories: null,
            co2: null,
          }
    );
    const [selectedIngredientsItems, setSelectedIngredientsItems] = useState(
      existingTableData.some((data) => data.station_name?.id == station?.id)
        ? stationMenu?.recipe_id?.ingredients_list
            ?.filter((e) => e?.new_climate_db != null)
            .map((item, i) => {
              return {
                name: item?.new_climate_db?.name,
                Value2: item?.unit,
                item: item,
                Item1: item?.new_climate_db,
                IndexId: i,
                prev1: item?.new_climate_db,
                prev2: item?.unit,
              };
            }) ?? []
        : []
    );
    const [portionsProducedValue, setPortionsProducedValue] = useState(
      existingTableData?.some((data) => data?.station_name?.id == station?.id)
        ? stationMenu?.portions_produced ?? ""
        : ""
    );
    const [portionsSoldValue, setPortionsSoldValue] = useState(
      existingTableData?.some((data) => data?.station_name?.id == station?.id)
        ? stationMenu?.portions_sold ?? ""
        : ""
    );
    const MAX_LIMIT = 100;
    const MAX_ALERGEN_LIST = 14;
    const MAX_DIETARY_LIST = 8;
    useEffect(() => {
      if (translatedMenuInfo?.length > MAX_LIMIT) {
        setTranslatedMenuInfo(translatedMenuInfo?.substring(0, MAX_LIMIT));
      }
    }, [translatedMenuInfo?.length]);

    const handleAllergens = (id) => {
      if (selectedMenuItems.indexOf(id) == -1) {
        if (selectedMenuItems?.length < MAX_ALERGEN_LIST) {
          setSelectedMenuItems([...selectedMenuItems, id]);
        }
      } else {
        setSelectedMenuItems(selectedMenuItems?.filter((e) => e != id));
      }
    };
    const handleDietary = (id) => {
      if (selectedDietaryItems.indexOf(id) == -1) {
        if (selectedDietaryItems?.length < MAX_DIETARY_LIST) {
          setSelectedDietaryItems([...selectedDietaryItems, id]);
        }
      } else {
        setSelectedDietaryItems(selectedDietaryItems?.filter((e) => e != id));
      }
    };

    useImperativeHandle(_ref, () => ({
      getSelectedMenuItems: () => {
        return selectedMenuItems;
      },
      getSelectedDietaryItems: () => {
        return selectedDietaryItems;
      },
      getMenuInfo: () => {
        return menuInfo?.trim();
      },
      getTranslatedMenuInfo: () => {
        return translatedMenuInfo?.trim();
      },
      getMenuId: () => {
        return stationMenu?.id;
      },
      getTotalCount: () => {
        return menuInfo?.trim()?.length;
      },
      getTranslatedMenuInfoTotalCount: () => {
        return translatedMenuInfo?.trim()?.length;
      },
      getErrorStatus: () => {
        return menuInfo?.trim()?.length > 0 &&
          translatedMenuInfo?.trim()?.length === 0
          ? true
          : isError;
      },
    }));
    const TitleCheck = station?.station_name_translation?.filter(
      (e) => e?.location_id === locationId
    );
    const Title = TitleCheck?.[0]?.name?.length
      ? TitleCheck?.[0]?.name
      : station?.station_name;
    const TitleCheckStationMenu =
      stationMenu?.station_name?.station_name_translation?.filter(
        (e) => e?.location_id === locationId
      );
    const TitleStationMenu = TitleCheckStationMenu?.[0]?.name?.length
      ? TitleCheck?.[0]?.name
      : stationMenu?.station_name?.station_name;

    const ModelHandle = () => {
      preventDouble != true && setMenuIsOpen(true);
    };
    const DkTitle = () => (
      <h2
        className={`text-black uppercase font-normal ${
          station?.is_not_main_station === true &&
          station?.is_dressings == false
            ? "xs-subTitle text-[12px]"
            : ""
        } ${station?.is_dressings == true ? "xs-subTitle text-[12px]" : ""}`}
      >
        {existingTableData.some((data) => data.station_name?.id == station?.id)
          ? TitleStationMenu
          : Title}
        {station?.is_not_main_station === true
          ? " DK"
          : station?.is_parent_station === true
          ? ""
          : " DK"}
      </h2>
    );
    const EnTitle = (prop) => {
      return (
        (prop == false ? station?.is_parent_station === false : true) && (
          <h2
            className={`text-black uppercase font-normal ${
              station?.is_not_main_station === true &&
              station?.is_dressings == false
                ? "xs-subTitle text-[12px]"
                : ""
            } ${
              station?.is_dressings == true ? "xs-subTitle text-[12px]" : ""
            }`}
          >
            {existingTableData.some(
              (data) => data.station_name?.id == station?.id
            )
              ? TitleStationMenu
              : Title}
            {station?.is_not_main_station === true
              ? " EN"
              : station?.is_parent_station === true
              ? ""
              : " EN"}
          </h2>
        )
      );
    };
    const stationName = existingTableData?.some(
      (data) => data?.station_name?.id == station?.id
    )
      ? TitleStationMenu
      : Title;
    const menuPresent =
      menuInfo?.trim()?.length > 0 || translatedMenuInfo?.trim()?.length > 0
        ? true
        : false;
    return (
      <>
        <div
          className={`flex flex-row lg:gap-8 md:gap-5 ${
            bottomLine ? "border-b border-b-[#D0D0D0] mb-5 pb-6" : ""
          }`}
        >
          <div className="card w-1/3 pointer-events-none">
            <div className="card col-span-12 md:col-span-12 lg:col-span-4 mb-4">
              <div className="mb-2">
                <h2
                  className={`text-black font-normal uppercase ${
                    station?.is_not_main_station === true &&
                    station?.is_dressings == false
                      ? "xs-subTitle text-[12px]"
                      : ""
                  } ${
                    station?.is_dressings == true
                      ? "xs-subTitle text-[12px]"
                      : ""
                  }`}
                >
                  {stationName}
                  {station?.is_not_main_station === true
                    ? " DK"
                    : station?.is_parent_station === true
                    ? ""
                    : " DK"}
                </h2>
              </div>
              {station?.is_parent_station !== true ? (
                <div
                  className={`border bg-white relative border-${
                    menuInfo?.length <= MAX_LIMIT ? "gray" : "red"
                  }-300 border-l-8 border-l-[#B6D989] px-3 pl-2 py-2`}
                  style={{
                    borderLeftColor:
                      stationColor[indexValue] ?? stationColor?.slice(-1)?.[0],
                    borderLeftWidth: "6px",
                  }}
                >
                  <CustomInput
                    lang={true}
                    value={menuInfo}
                    setValue={setMenuInfo}
                    setAllergens={setSelectedMenuItems}
                    setDietary={setSelectedDietaryItems}
                    setIngredient={setSelectedIngredientsItems}
                  />

                  <span className="block text-xs text-[#AAA8A8] font-medium">
                    {menuInfo?.length}/{MAX_LIMIT} characters
                  </span>
                </div>
              ) : null}
            </div>
            <div className="card col-span-12 md:col-span-12 lg:col-span-4 ">
              <div className="mb-2">
                {station?.is_parent_station === false && (
                  <h2
                    className={`text-black uppercase font-normal ${
                      station?.is_not_main_station === true &&
                      station?.is_dressings == false
                        ? "xs-subTitle text-[12px]"
                        : ""
                    } ${
                      station?.is_dressings == true
                        ? "xs-subTitle text-[12px]"
                        : ""
                    }`}
                  >
                    {existingTableData?.some(
                      (data) => data?.station_name?.id == station?.id
                    )
                      ? TitleStationMenu
                      : Title}
                    {station?.is_not_main_station === true
                      ? " EN"
                      : station?.is_parent_station === true
                      ? ""
                      : " EN"}
                  </h2>
                )}
              </div>
              {station?.is_parent_station !== true ? (
                <div
                  className={`border bg-white relative border-${
                    translatedMenuInfo?.length <= MAX_LIMIT ? "gray" : "red"
                  }-300 border-l-8 border-l-[#B6D989] px-3 pl-2 py-2`}
                  style={{
                    borderLeftColor:
                      stationColor[indexValue] ?? stationColor?.slice(-1)?.[0],
                    borderLeftWidth: "6px",
                  }}
                >
                  <CustomInput
                    lang={false}
                    value={translatedMenuInfo}
                    setValue={translatedMenuInfo}
                    setAllergens={setSelectedMenuItems}
                    setDietary={setSelectedDietaryItems}
                    setIngredient={setSelectedIngredientsItems}
                  />
                  <span className="block text-xs text-[#AAA8A8] font-medium">
                    {translatedMenuInfo?.length}/{MAX_LIMIT} characters
                  </span>
                </div>
              ) : null}
            </div>
          </div>
          <div className="card w-1/3 lg:px-[15px] md:px-[5px] pointer-events-none relative">
            {station?.is_parent_station !== true ? (
              <div className="mb-2">
                <h2 className="text-black capitalize font-semibold text-sm">
                  Allergener
                </h2>
              </div>
            ) : null}
            {station?.is_parent_station !== true ? (
              <div className="list-items">
                <ul className="lists flex flex-wrap gap-2 grid-cols-8 sm:grid-cols-6 md:grid-cols-8 1xl:grid-cols-6">
                  {editContent?.getAllAllergens?.data?.map(
                    (allergen, index) => (
                      <li
                        key={index}
                        onClick={() => handleAllergens(allergen?.id)}
                        className={`list cursor-pointer text-center text-xs px-3 py-1 rounded-[24px] border ${
                          selectedMenuItems?.some(
                            (item) => item == allergen?.id
                          )
                            ? "bg-primary text-white"
                            : null
                        } border-primary col-span-4 sm:col-span-2 md:col-span-1 lg:col-span-2 1xl:col-span-1 font-normal`}
                      >
                        {allergen?.name}
                      </li>
                    )
                  )}
                </ul>
              </div>
            ) : null}
            
            {totalCalorie?.calories !== null && totalCalorie?.calories > 0 && (
              <div className="co2-section bg-[#E1EEDE] text-xs text-black rounded-[24px] py-1 px-4 inline-block mt-[0.75rem]">
                <div className="calorie font-medium">
                  Kalorier: {totalCalorie?.calories} kcal
                </div>
              </div>
            )}
            {selectedMenuItems?.length >= MAX_ALERGEN_LIST && (
              <div className="text-red-500 text-sm text-left mt-2">
                {`*Maximum ${MAX_ALERGEN_LIST} allergener are allowed.`}
              </div>
            )}
          </div>
          {/* Dietary */}
          <div className="card w-1/3 lg:px-[15px] md:px-[5px] pointer-events-none relative">
            {station?.is_parent_station !== true ? (
              <div className="mb-2">
                <h2 className="text-black capitalize font-semibold text-sm">
                  {DIETARY_PREFERENCES_DK}
                </h2>
              </div>
            ) : null}
            {station?.is_parent_station !== true ? (
              <div className="list-items">
                <ul className="lists flex flex-wrap gap-2 grid-cols-8 sm:grid-cols-6 md:grid-cols-8 1xl:grid-cols-6">
                  {editContent?.getAllDietary?.data?.map((dietary, index) => (
                    <li
                      key={index}
                      onClick={() => handleDietary(dietary?.id)}
                      className={`list cursor-pointer text-center text-xs px-3 py-1 rounded-[24px] border ${
                        selectedDietaryItems?.some(
                          (item) => item == dietary?.id
                        )
                          ? "bg-primary text-white"
                          : null
                      } border-primary col-span-4 sm:col-span-2 md:col-span-1 lg:col-span-2 1xl:col-span-1 font-normal`}
                    >
                      {dietary?.name}
                    </li>
                  ))}
                </ul>
              </div>
            ) : null}
            {totalCalorie?.co2 !== null && totalCalorie?.co2 > 0 && (
              <div className="co2-section bg-[#E1EEDE] text-xs text-black rounded-[24px] py-1 px-4 inline-block mt-[0.75rem]">
                <div className="calorie font-medium">
                  CO2: {totalCalorie?.co2} kg
                </div>
              </div>
            )}
            {selectedDietaryItems?.length >= MAX_DIETARY_LIST && (
              <div className="text-red-500 text-sm text-left mt-2">
                {`*Maximum ${MAX_DIETARY_LIST} kostpræferencer are allowed.`}
              </div>
            )}
          </div>
          {station?.is_parent_station !== true && (
            <button
              className={`w-[120px] self-start p-1 rounded-md text-primary text-sm font-medium cursor-pointer ${
                stationMenu?.id ? " bg-[#F1F1F1]" : " bg-[#E3E3AD]"
              }`}
              onClick={() =>
                station?.is_parent_station !== true && ModelHandle()
              }
            >
              {stationMenu?.id ? "Edit Recipe" : "Add Recipe"}
            </button>
          )}
        </div>
        {menuModalIsOpen ? (
          <EditMenuModel
            ref={_ref}
            key={stationMenu?.id}
            station={stationMenu}
            editContent={editContent}
            existingTableData={existingTableData}
            indexValue={indexValue}
            stationColor={stationColor}
            setIsOpenv={setMenuIsOpen}
            EnTitle={EnTitle}
            DkTitle={DkTitle}
            dkMenu={menuInfo}
            setDkMenu={setMenuInfo}
            enMenu={translatedMenuInfo}
            setEnMenu={setTranslatedMenuInfo}
            allergensList={selectedMenuItems}
            setAllergensList={setSelectedMenuItems}
            dietaryList={selectedDietaryItems}
            setDietaryList={setSelectedDietaryItems}
            ingredientsList={selectedIngredientsItems}
            setIngredientsList={setSelectedIngredientsItems}
            portionsProducedValue={portionsProducedValue}
            setPortionsProducedValue={setPortionsProducedValue}
            portionsSoldValue={portionsSoldValue}
            setPortionsSoldValue={setPortionsSoldValue}
            totalCalorie={totalCalorie}
            setTotalCalorie={setTotalCalorie}
            menuId={{
              menu: stationMenu?.id,
              recipeId: stationMenu?.recipe_id?.id,
            }}
            stationId={stationId}
            stationName={stationName}
          />
        ) : null}
      </>
    );
  }
);

export default EditMenuItem;
