import React, { useState } from "react";
import Autocomplete from "react-autocomplete";
import {
  directusLoaderLink,
  ALLERGENS_ENG,
  ALLERGENS_ENG_DK,
  DIETARY_PREFERENCES_ENG,
  DIETARY_PREFERENCES_DK,
} from "~/utils/constants";
import { getAssetURL } from "~/utils/get-asset-url";

function CustomInput({
  lang,
  value,
  setValue,
  setAllergens,
  setDietary,
  setIngredient,
  valueEng,
  setValueEng,
  setBgRemovedImage,
}) {
  const [items, setItems] = useState([]);
  React.useEffect(() => {
    if (value?.includes("@")) {
      searchMenuRender({ value: value, type: lang });
    }
  }, [value?.includes("@")]);
  const getMentionedUsers = () => {
    const startIndex = value?.lastIndexOf("@");
    const mentionText = value?.substring(startIndex + 1);
    if (startIndex !== -1) {
      const val = items?.filter((item) =>
        (lang ? item?.menu_info : item?.menu_info_1)
          ?.toLowerCase()
          ?.includes(mentionText?.toLowerCase())
      );
      return val?.slice(0, 10);
    } else {
      return [];
    }
  };

  const searchMenuRender = async (type) => {
    const menuInfo1 = type?.value;
    const typeInput = type?.type === "da" ? true : false;
    if (menuInfo1?.includes("@") && menuInfo1.length <= 2) {
      var filterValue = {};
      if (menuInfo1.includes("@") && menuInfo1.length >= 1) {
        filterValue = typeInput
          ? { menu_info: { _nempty: null }, is_global: { _eq: true } }
          : { menu_info_1: { _nempty: null }, is_global: { _eq: true } };
      }
      await directusLoaderLink
        .items("recipe_book")
        .readByQuery({
          fields: [
            "*",
            "allergens_list.id",
            "allergens_list.Allergens_id.*",
            "dietary_list.id",
            "dietary_list.Dietary_id.*",
            "ingredients_list.*",
            "ingredients_list.new_climate_db.*",
          ],
          filter: filterValue,
          sort: ["id"],
        })
        .then((res) => {
          setItems(res?.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }
    return [];
  };
  const renderItem = (item, isHighlighted) => {
    const menu = lang ? item?.menu_info : item?.menu_info_1;
    return (
      <div
        className="p-2 block overflow-hidden border border-b-[#c5c5c5]"
        key={item?.id}
        style={{ background: isHighlighted ? "lightgray" : "white" }}
      >
        <div className="float-left w-[80%] mr-2">
          <div>{menu}</div>
          {item?.allergens_list?.length > 0 && (
            <div>
              <span className=" font-bold">
                {lang ? ALLERGENS_ENG_DK : ALLERGENS_ENG}
                {": "}
              </span>
              {item?.allergens_list?.map(
                (e, i) =>
                  `${e?.Allergens_id?.[lang ? "name" : "translated_name"]}${
                    i !== item?.allergens_list?.length - 1 ? ", " : ""
                  }`
              )}
            </div>
          )}
          {item?.dietary_list?.length > 0 && (
            <div>
              <span className=" font-bold">
                {lang ? DIETARY_PREFERENCES_DK : DIETARY_PREFERENCES_ENG}
                {": "}
              </span>
              {item?.dietary_list?.map(
                (e, i) =>
                  `${e?.Dietary_id?.[lang ? "name" : "translated_name"]}${
                    i !== item?.dietary_list?.length - 1 ? ", " : ""
                  }`
              )}
            </div>
          )}
        </div>
        {item?.app_resultant_image !== null && (
          <div className="float-right w-[75px] text-right">
            <img
              src={getAssetURL(item?.app_resultant_image)}
              alt={"dish"}
              style={{ width: "60px", height: "60px" }}
            />
          </div>
        )}
      </div>
    );
  };
  return (
    <div className="w-full">
      <Autocomplete
        wrapperStyle={{ width: "100%" }}
        getItemValue={(item) => {
          return JSON.stringify(item);
        }}
        items={getMentionedUsers()}
        renderItem={renderItem}
        value={value}
        onChange={(e) => setValue(e?.target?.value)}
        onSelect={(value) => {
          const val = JSON.parse(value);
          setAllergens(val?.allergens_list?.map((e) => e?.Allergens_id?.id));
          setDietary(val?.dietary_list?.map((e) => e?.Dietary_id?.id));
          setIngredient(
            val?.ingredients_list?.map((item, i) => {
              return {
                name: item?.new_climate_db?.name,
                Value2: item?.unit,
                item: item,
                Item1: item?.new_climate_db,
                IndexId: i,
                prev1: item?.new_climate_db,
                prev2: item?.unit,
              };
            })
          );
          const menu = lang ? val?.menu_info : val?.menu_info_1;
          const startIndex = menu?.lastIndexOf("@");
          const newValue =
            menu?.substring(0, startIndex + 1) +
            menu?.substring(startIndex + 1, menu?.length);
          setValue(newValue);
          const menu1 = !lang ? val?.menu_info : val?.menu_info_1;
          const startIndex1 = menu?.lastIndexOf("@");
          const newValue1 =
            menu1?.substring(0, startIndex1 + 1) +
            menu1?.substring(startIndex1 + 1, menu1?.length);
          setValueEng(newValue1);
          val?.app_resultant_image !== null &&
            setBgRemovedImage({
              img: val?.app_resultant_image,
              type: "id",
              update: true,
            });
        }}
        renderInput={(e) => <textarea {...e} onKeyDown={(e) => {}} />}
        menuStyle={{
          backgroundColor: "black",
          position: "absolute",
          top: "50px",
          left: "0",
          zIndex: 20,
          maxHeight: "300px",
          overflowY: "auto",
          width: "100%",
          display: `${value?.includes("@") ? "block" : "none"}`,
        }}
        inputProps={{
          style: { width: "100%", height: "70px", resize: "unset" },
          placeholder: "Add meal...",
        }}
      />
    </div>
  );
}

export default CustomInput;
