import React, { useEffect, useRef, useState } from "react";
import { json } from "@remix-run/node";
import { Link, useLoaderData } from "@remix-run/react";
import HeaderFile from "~/components/HeaderFile";
import Footer from "~/components/Footer";
import { getDirectusClient } from "~/lib/directus";
import EditMenuItem from "~/components/edit/editMenuItem";
import {
  directusLoaderLink,
  SECTION_6_ID,
  SECTION_6_1_ID,
  SECTION_6_2_ID,
  SECTION_6_3_ID,
  SECTION_7_ID,
  SECTION_7_1_ID,
  SECTION_7_2_ID,
  SECTION_7_3_ID,
  SCREENCLOUD_CACHE_CLEAR_API_URL,
  PURGE_CACHE_URL,
  FRONTEND_URL,
} from "~/utils/constants.js";
import { sessionStorage } from "~/services/session.server";
import { toast } from "react-toastify";
import { authLogout, isLoggedIn } from "~/utils/db.server";
import Loader from "~/components/loader";
import { format } from "date-fns";
export const action = async ({ request }) => {
  await authLogout(request);
};

export const loader = async ({ request, params }) => {
  const dateId = params.date;
  await isLoggedIn(request);
  const session = await sessionStorage.getSession(
    request.headers.get("Cookie")
  );
  var sessionkey = session.get("sessionKey");
  const directus = await getDirectusClient(sessionkey);
  const currentUser = await directus.users.me.read({
    fields: [
      "*.*.*",
      "selected_locations.allowed_stations.Locations_id.*",
      "selected_locations.allowed_stations.Stations_id.*",
    ],
  });
  const LocationId = currentUser?.selected_locations?.id
    ? currentUser?.selected_locations?.id
    : currentUser?.allowed_locations?.[0]?.Locations_id?.id;

  const allowedStationUniqueId =
    currentUser?.selected_locations?.allowed_stations?.map(
      (e) => e?.Stations_id?.station_unique_id
    );
  allowedStationUniqueId?.includes(SECTION_6_ID?.toString())
    ? [
        SECTION_6_1_ID?.toString(),
        SECTION_6_2_ID?.toString(),
        SECTION_6_3_ID?.toString(),
      ]?.map((e) => allowedStationUniqueId?.push(e))
    : "";
  allowedStationUniqueId?.includes(SECTION_7_ID?.toString())
    ? [
        SECTION_7_1_ID?.toString(),
        SECTION_7_2_ID?.toString(),
        SECTION_7_3_ID?.toString(),
      ]?.map((e) => allowedStationUniqueId?.push(e))
    : "";
  const resBodyFilter =
    allowedStationUniqueId?.length > 0
      ? {
          date: { _eq: dateId },
          station_name: {
            station_unique_id: { _in: allowedStationUniqueId },
          },
          location_id: {
            id: {
              _eq: LocationId,
            },
          },
        }
      : {
          date: { _eq: dateId },
          location_id: {
            id: {
              _eq: currentUser?.selected_locations?.id
                ? currentUser?.selected_locations?.id
                : currentUser?.allowed_locations?.[0]?.Locations_id?.id,
            },
          },
        };

const response = await directus.items("screen_display").readByQuery({
    fields: [
      "*.*.",
      "recipe_id.app_resultant_image.*",
      "recipe_id.allergens_list.*",
      "recipe_id.allergens_list.Allergens_id.*",
      "recipe_id.dietary_list.*",
      "recipe_id.ingredients_list.*",
      "recipe_id.ingredients_list.new_climate_db.*",
      "recipe_id.dietary_list.Dietary_id.*",
      "station_name.*",
      'station_name.station_image_1.*.*'
    ],
    filter: resBodyFilter,
  });

  const getAllAllergens = await directus
    .items("Allergens")
    .readByQuery({ fields: ["*.*"] });
  const getAllDietary = await directus
    .items("Dietary")
    .readByQuery({ fields: ["*.*"] });
  const allStationBody =
    allowedStationUniqueId?.length > 0
      ? {
          fields: ["*", "child_list.related_Stations_id.id","parent_id.id","parent_id.station_name","parent_id.station_name_translation",'station_name.station_image_1.*.*'],
          filter: {
            station_unique_id: { _in: allowedStationUniqueId },
          },
        }
      : { fields: ["*"] };
  const getAllStations = await directus
    .items("Stations")
    .readByQuery(allStationBody);
  return json({
    date: dateId,
    res: response,
    getAllAllergens,
    getAllDietary,
    getAllStations: getAllStations?.data?.sort(
      (a, b) => parseFloat(a?.sequence) - parseFloat(b?.sequence)
    ),
    currentUser,
    locationId: LocationId,
  });
};

export function meta() {
  return { title: "Edit Menu of the day" };
}

export default function EditMenuOfTheDayPage({ params }) {
  const editContent = useLoaderData();
  const stationColor = editContent?.getAllStations?.map((e) => e.tagColor);
  const [auth, setAuth] = useState(true);
  const [open, setOpen] = useState(true);
  const [open2, setOpen2] = useState(true);
  const [preventDouble, setPreventDouble] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [modalIsOpen, setIsOpen] = useState(false);
  const existingTableData = editContent?.res?.data;
  const editMenuItemRefs = useRef([]);
  const buttonRef = useRef(null);
  const dateFormat = "EEEE";
  const [stationMenuList, setStationMenuList] = useState(
    editContent?.getAllStations
  );
  
  const cloudFlareClean=async()=>{
    const response = await directusLoaderLink
    .items("Locations")
    .readByQuery({
      fields: ["screen_ids"],
      filter: { id: { _eq: editContent?.locationId } },
    });
  //set time out to call cache clear

  setTimeout(async () => {
    if (response?.data?.[0]?.screen_ids?.length > 0) {
      const arrayOfScreenIds =response?.data?.[0]?.screen_ids?.split(",");
      
      await fetch(SCREENCLOUD_CACHE_CLEAR_API_URL, {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        credentials: "same-origin",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "follow",
        referrerPolicy: "no-referrer",
        body: JSON.stringify(arrayOfScreenIds),
      })
        .then((e) => {})
        .catch((e) => {
          console.log(e);
        });
    }
    await fetch(PURGE_CACHE_URL, {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      headers: {
        "Content-Type": "application/json",
      },
      redirect: "follow",
      referrerPolicy: "no-referrer",
      body: JSON.stringify({
        files: {
          files: [`${FRONTEND_URL}/`],
        },
      }),
    })
      .then((e) => {})
      .catch((e) => {
        console.log(e);
      });
  }, 3000);
  }
  const closeMenuOfDay = (date) => {
    localStorage.setItem("LastWacthedWeek", editContent.date);
  };

  useEffect(() => {
    localStorage.setItem("LastWacthedWeek", editContent.date);
    const MenuSave= localStorage.getItem("MenuSave");
    if(MenuSave?.length>0){
      MenuSave==='create'?toast.success(`The record has been saved successfully`): (MenuSave==='edit'&&
       toast.success(`The record has been updated successfully`));
       localStorage.removeItem("MenuSave")
       cloudFlareClean()
    }
    var isValue = false;
    const interval = setInterval(() => {
      for (let i = 0; i < stationMenuList?.length; i++) {
        if (editMenuItemRefs?.current[i]?.getTotalCount() > 0) {
          isValue = true;
          if (
            editMenuItemRefs?.current[i]?.getTranslatedMenuInfoTotalCount() > 0
          ) {
            clearInterval(interval);
            setIsLoading(false);
          } else {
            !isLoading && setIsLoading(true);
          }
        }
        if (editMenuItemRefs?.current[i]?.getErrorStatus()) {
          clearInterval(interval);
          setIsLoading(false);
          toast.error("Oops!!! Something went wrong in the google translation");
          break;
        }
      }
      if (!isValue) {
        clearInterval(interval);
        setIsLoading(false);
      }
    }, 200);
    return () => clearInterval(interval);
  }, []);

  let childArray = [];
  let childDataArray = [];
  return (
    <div>
      <HeaderFile title logout={setAuth} noLeftLogo={false} />
      <div className="main-content-area">
        <div className="page__wrap justify-center items-center px-5 py-4 lg:px-10 p-10 pt-7 bg-[#F8F8F8]">
          <div className="head pt-9 pb-5 text-center text-primary">
            <h1 className="text-lg font-medium">
              {format(new Date(editContent.date), dateFormat)}{" "}
              {format(new Date(editContent.date), "MM/dd/yyyy")}
            </h1>
            <p className="mt-1 text-sm font-normal">
              Menu of the day
            </p>
          </div>
          <div className="relative w-full bg-black ">
            <div>
              <button className="absolute flex bottom-0 left-0 mb-3">
                <Link
                  className="flex-1 d-flex justify-items-end"
                  to={{ pathname: "/", state: { id: "test" } }}
                  onClick={() => closeMenuOfDay()}
                >
                  <span className="w-full min-w-[112px] py-1.5 flex justify-start items-center text-primary font-medium text-sm rounded  focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary">
                  <i className="ri-arrow-left-line mr-1"></i>  Go Back
                  </span>
                </Link>
              </button>
            </div>
          </div>
          <div className=" relative">
            {/* <div className="grid grid-cols-12 gap-6"> */}
            {isLoading ? (
              <div
                style={{
                  flex: 1,
                  justifyContent: "center",
                  alignItems: "center",
                  minHeight: "100vh",
                }}
              >
                <Loader />
              </div>
            ) : null}
            <div
              style={{
                display: isLoading ? "none" : "contents",
              }}
            >
              {stationMenuList?.map((stationMenu, index) => {
                const allowedStations =
                  editContent?.currentUser?.selected_locations?.allowed_stations?.map(
                    (e) =>
                      e?.Stations_id?.is_not_main_station !== true &&
                      e?.Stations_id?.station_unique_id
                  );
                const mainStation = allowedStations?.filter(
                  (e) =>
                    e === stationMenu?.station_unique_id &&
                    e?.is_not_main_station !== true
                );
                const val = new Array(allowedStations)[0]; //object to array
                const subStation =
                  (stationMenu?.station_unique_id ===
                    SECTION_6_1_ID?.toString() &&
                    val?.includes(SECTION_6_ID?.toString())) ||
                  (stationMenu?.station_unique_id ===
                    SECTION_6_2_ID?.toString() &&
                    val?.includes(SECTION_6_ID?.toString())) ||
                  (stationMenu?.station_unique_id ===
                    SECTION_6_3_ID?.toString() &&
                    val?.includes(SECTION_6_ID?.toString())) ||
                  (stationMenu?.station_unique_id ===
                    SECTION_7_1_ID?.toString() &&
                    val?.includes(SECTION_7_ID?.toString())) ||
                  (stationMenu?.station_unique_id ===
                    SECTION_7_2_ID?.toString() &&
                    val?.includes(SECTION_7_ID?.toString())) ||
                  (stationMenu?.station_unique_id ===
                    SECTION_7_3_ID?.toString() &&
                    val?.includes(SECTION_7_ID?.toString()));
                if (
                  subStation ||
                  mainStation[0]
                  // &&
                  // (stationMenu?.station_unique_id !==
                  //   SECTION_7_ID?.toString() ||
                  //   Urldate(editContent?.date) ===
                  //     Urldate(
                  //       lastDayOfWeek(new Date(editContent?.date), {
                  //         weekStartsOn: 6,
                  //       })
                  //     ))
                ) {
                  stationMenu?.is_parent_station === false;
                  if (stationMenu?.child_list?.length) {
                    childArray = [];
                    childDataArray = [];
                    stationMenu?.child_list?.map((e) =>
                      childArray.push(e?.related_Stations_id)
                    );
                  }
                  childArray?.length &&
                    childDataArray?.push({
                      stationMenu: stationMenu,
                      existingTableData: existingTableData,
                      stationColor: stationColor,
                      index: index,
                    });
                  if (
                    stationMenu?.is_not_main_station == false &&
                    stationMenu?.is_parent_station == false
                  ) {
                  return (
                      <div
                        className="editmenuitem bg-white rounded-[10px] border border-[#E2E2E2] shadow-sm px-5 py-10 lg:px-10 mb-5"
                        key={index}
                      >
                        <EditMenuItem
                          ref={(el) => (editMenuItemRefs.current[index] = el)}
                          station={stationMenu}
                          editContent={editContent}
                          existingTableData={existingTableData}
                          indexValue={index}
                          setOpen={setOpen}
                          setOpen2={setOpen2}
                          locationId={editContent?.locationId}
                          stationColor={stationColor}
                          bottomLine={false}
                          preventDouble={preventDouble}
                          stationId={{stationId:stationMenu?.id,uniqueId:stationMenu?.station_unique_id}}
                        />
                      </div>
                    );
                  }
                  if (childArray?.length + 1 == childDataArray?.length) {
                    return (
                      <div
                        className="editmenuitem bg-white rounded-[10px] border border-[#E2E2E2] shadow-sm px-5 py-10 lg:px-10 mb-5"
                        key={index}
                      >
                        {childDataArray?.map((e, i) => {
                          return (
                            <EditMenuItem
                              ref={(el) =>
                                (editMenuItemRefs.current[e?.index] = el)
                              }
                              key={e?.stationMenu?.id}
                              stationId={{stationId:e?.stationMenu?.id,uniqueId:e?.stationMenu?.station_unique_id}}
                              station={e?.stationMenu}
                              editContent={editContent}
                              existingTableData={e?.existingTableData}
                              indexValue={e?.index}
                              setOpen={setOpen}
                              setOpen2={setOpen2}
                              locationId={editContent?.locationId}
                              stationColor={e?.stationColor}
                              bottomLine={
                                e?.stationMenu?.is_parent_station !== true &&
                                i < childDataArray?.length - 1
                                  ? true
                                  : false
                              }
                              preventDouble={preventDouble}
                            />
                          );
                        })}
                      </div>
                    );
                  }
                } else {
                  return null;
                }
              })}
            </div>
            {/* </div> */}
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}
