import React, { useEffect, useState, useLayoutEffect } from "react";
import Modal from "react-modal";
import Todoingredients from "./todoIngredients";
import {
  GOOGLE_TRANSLATION_APIKEY,
  GOOGLE_TRANSLATOR_URL,
  EXPRESS_BASE_URL,
  URL,
} from "~/utils/constants";
import { directusLoaderLink } from "~/utils/constants.js";
import { toast } from "react-toastify";
import { decode, encode } from "~/utils/get-asset-url";
import CustomInput from "./customInput";
import BannerPopup from "../banner/bannerPopup";
import { getAssetURL } from "~/utils/get-asset-url";

const EditMenuModel = React.forwardRef((prop, _ref) => {
  const {
    setIsOpenv,
    station,
    editContent,
    existingTableData,
    indexValue,
    stationColor,
    EnTitle,
    DkTitle,
    dkMenu,
    enMenu,
    allergensList,
    dietaryList,
    ingredientsList,
    setIngredientsList,
    portionsProducedValue,
    portionsSoldValue,
    totalCalorie,
    menuId,
    stationId,
    stationName,
  } = prop;
  const user = {
    email: editContent?.currentUser?.email,
    password: editContent?.currentUser?.password_copy,
  };
  const bgImg =
    station?.app_resultant_image !== null
      ? { img: station?.app_resultant_image?.id, type: "id", update: true }
      : station?.recipe_id?.app_resultant_image?.id !== undefined
      ? {
          img: station?.recipe_id?.app_resultant_image?.id,
          type: "id",
          update: true,
        }
      : "";
  const [modalIsOpen, setIsOpen] = useState(setIsOpenv ? true : false);
  const [bannerModal, setBannerModal] = useState(false);
  const [ingredient, setIngredient] = useState(ingredientsList);
  const [ingredientChanged, setIngredientChanged] = useState(false);
  const [portionsProduced, setPortionsProduced] = useState(portionsProducedValue);
  const [portionsSold, setPortionsSold] = useState(portionsSoldValue)  
  const [isError, setIsError] = useState(false);
  const [menuInfo, setMenuInfo] = useState(dkMenu);
  const [bgImage, setBgImage] = useState("");
  const [bgImageRefreshError, setBgImageRefreshError] = useState("");
  const [bgRemovedImage, setBgRemovedImage] = useState(bgImg);
  const [saveType, setSaveType] = useState("day");
  const [translatedMenuInfo, setTranslatedMenuInfo] = useState(enMenu);
  const [table, showTable] = useState("Ingredienslist");
  const allergensNames = ['sesame', 'gluten', 'soy', 'mustard', 'nuts', 'egg', 'fish', 'celery', 'mollusks', 'milk', 'crustacea', 'lupine', 'peanuts', 'sulfur dioxide']
  const MAX_LIMIT = 100;
  const MAXALERGENLIST = 14;
  const MAXDIETARYLIST = 8;
  let fromLang = "da";
  let toLang = "en";
  const MAX_SIZE = 5000000; //5MB in bytes
  const customStyles = {
    content: {
      top: "50%",
      left: "50%",
      right: "auto",
      bottom: "auto",
      marginRight: "-50%",
      transform: "translate(-50%, -50%)",
      padding: "0px",
      width: "96%",
      maxHeight: "90vh",
    },
  };
  const [selectedMenuItems, setSelectedMenuItems] = useState(allergensList);
  const [selectedDietaryItems, setSelectedDietaryItems] = useState(dietaryList);
  const [preventDouble, setPreventDouble] = useState(false);
  const [preventDoubleImage, setPreventDoubleImage] = useState(false);
  const parentStationName = editContent?.getAllStations?.filter(
    (e) => e?.station_unique_id === stationId?.uniqueId
  )?.[0]?.parent_id;
  const closeModal = () => {
    const newList = [];
    ingredientsList?.map((e) =>
      newList.push({
        IndexId: e?.IndexId,
        Item1: e?.prev1,
        name: e?.prev1?.name,
        Value2: e?.prev2,
        item: e?.item,
        prev1: e?.prev1,
        prev2: e?.prev2,
      })
    );

    setIngredientsList(newList);
    setIsOpenv(false);
    setIsOpen(false);
  };
  const firstUpdate = React.useRef(true);
  async function GoogleTranslator(text) {
    try {
      let url = `${GOOGLE_TRANSLATOR_URL}/language/translate/v2?key=${GOOGLE_TRANSLATION_APIKEY}`;
      url += "&q=" + encode(text);
      url += `&source=${fromLang}`;
      url += `&target=${toLang}`;

      const res = await fetch(url, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      return res.json();
    } catch (err) {
      setIsError(true);
    }
  }
  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (menuInfo?.length > MAX_LIMIT) {
      setMenuInfo(menuInfo?.substring(0, MAX_LIMIT));
    } else {
      const callSearchService = () => {
        GoogleTranslator(menuInfo)
          .then((results) =>
            results?.data
              ? setTranslatedMenuInfo(
                  decode(
                    results?.data?.translations?.[0]?.translatedText?.replaceAll(
                      "%",
                      "%25"
                    )
                  )
                    ?.replaceAll("&#39;", "'")
                    ?.substring(0, MAX_LIMIT) ?? ""
                )
              : setIsError(true)
          )
          .catch((err) => console.log(err));
      };

      let debouncer = setTimeout(() => {
        callSearchService();
      }, 1000);
      return () => {
        clearTimeout(debouncer);
      };
    }
  }, [menuInfo.length]);

  useEffect(() => {
    if (translatedMenuInfo?.length > MAX_LIMIT) {
      setTranslatedMenuInfo(translatedMenuInfo?.substring(0, MAX_LIMIT));
    }
  }, [translatedMenuInfo.length]);
  bgImageRefreshError?.trim()?.length > 0 &&
    setTimeout(() => {
      setBgImageRefreshError("");
    }, 5000);

  useEffect(() => {
    ingredientChanged && autoAddAllergence();
  }, [ingredient])

  let totalCalories = 0;
  let totalCO2 = 0;
  ingredient?.length > 0 &&
    ingredient?.map((e) => {
      e?.Item1?.CO2e != null &&
        (totalCO2 =
          (Number(e?.Item1?.CO2e.replace(',', '.')) / 1000) * Number(e?.Value2) +
          totalCO2);
      e?.Item1?.energy != null &&
        e?.Item1?.energy != undefined &&
        (totalCalories =
          (Number(e?.Item1?.energy.replace(',', '.')) / 100) *
            0.239006 *
            Number(e?.Value2) +
          totalCalories);
      return "";
    });
  const handleAllergens = (id) => {
    if (selectedMenuItems.indexOf(id) == -1) {
      if (selectedMenuItems?.length < MAXALERGENLIST) {
        setSelectedMenuItems([...selectedMenuItems, id]);
      }
    } else {
      setSelectedMenuItems(selectedMenuItems.filter((e) => e != id));
    }
  };

  const autoAddAllergence = () => {
    const allergenIds = [];
    ingredient?.map((ingredient) => {
    allergensNames?.map((name)=> {
      if(['ja', 'spor af', 'afledt af']?.includes(ingredient?.Item1[name]?.toLowerCase())){
        const allergen = editContent?.getAllAllergens?.data?.filter((i) => i?.translated_name?.toLowerCase() === (name === 'egg' ? 'eggs' : name))[0]?.id
        !allergenIds.some(id => id === allergen) && allergenIds?.push(allergen)
      }
    }) 
    })
    setSelectedMenuItems(allergenIds);
  };

  const handleDietary = (id) => {
    if (selectedDietaryItems.indexOf(id) == -1) {
      if (selectedDietaryItems?.length < MAXDIETARYLIST) {
        setSelectedDietaryItems([...selectedDietaryItems, id]);
      }
    } else {
      setSelectedDietaryItems(selectedDietaryItems.filter((e) => e != id));
    }
  };
  // handle click event of the Add button
  const handleAddClick = () => {
    setIngredient([
      ...ingredient,
      { name: "", Value2: "", Item1: {}, IndexId: "" },
    ]);
  };

  const IngredientsTable = () => {
    return (
      <div className="tab-section-data">
        {ingredient?.length > 0 && (
          <span className="tab-ingredients block text-sm text-[#777] py-2">
            Ingredienser
          </span>
        )}
        <Todoingredients
          ingredient={ingredient}
          setIngredient={setIngredient}
          autoAddAllergence={autoAddAllergence}
          setIngredientChanged={setIngredientChanged}
        />
        <div className="flex gap-3 my-3">
          <button
            onClick={() => handleAddClick()}
            className="w-20 h-10 min-w-[40px] min-h-[40px] inline-flex items-center justify-start text-primary text-sm font-medium"
          >
            <svg
              viewBox="0 0 24 24"
              width="16"
              height="16"
              stroke="#023937"
              strokeWidth="3"
              fill="none"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="css-i6dzq1 mr-1"
            >
              <line x1="12" y1="5" x2="12" y2="19"></line>
              <line x1="5" y1="12" x2="19" y2="12"></line>
            </svg>
            Add
          </button>
          {/* <button
            className="future w-20 h-10 min-w-[40px] px-5 min-h-[40px] inline-flex items-center justify-center border border-transparent bg-primary text-sm font-medium text-white shadow-sm sm:w-auto opacity-60"
            disabled
          >
            <svg
              viewBox="0 0 24 24"
              width="16"
              height="16"
              stroke="#fff"
              strokeWidth="3"
              fill="none"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="css-i6dzq1 mr-1"
            >
              <line x1="12" y1="5" x2="12" y2="19"></line>
              <line x1="5" y1="12" x2="19" y2="12"></line>
            </svg>
            Brugerdefinerede ingredienser
          </button> */}
        </div>
      </div>
    );
  };
  const AllergensTable = () => {
    return (
      <div className="py-4">
        {station?.is_parent_station !== true ? (
          <ul className="lists flex flex-wrap gap-2 grid-cols-8 sm:grid-cols-6 md:grid-cols-8 1xl:grid-cols-6 max-w-[70%]">
            {editContent?.getAllAllergens?.data?.map((allergen, index) => (
              <li
                key={index}
                onClick={() => handleAllergens(allergen?.id)}
                className={`list cursor-pointer text-center text-xs px-3 py-1 rounded-[24px] border ${
                  selectedMenuItems?.some((item) => item == allergen?.id)
                    ? "bg-primary text-white"
                    : null
                } border-primary col-span-4 sm:col-span-2 md:col-span-1 lg:col-span-2 1xl:col-span-1 font-normal`}
              >
                {allergen?.name}
              </li>
            ))}
          </ul>
        ) : null}
        {ingredientChanged && (
          <button
          onClick={() => setIngredientChanged(false)}
          className="min-w-[176px] min-h-[30px] inline-flex items-center justify-center border border-transparent bg-primary text-sm font-medium text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 sm:w-auto"
        >
          {"Save"}
        </button>
        )}
        {selectedMenuItems?.length >= MAXALERGENLIST && (
          <div className="text-red-500 text-sm text-right">
            {`*Maximum ${MAXALERGENLIST} allergener are allowed.`}
          </div>
        )}
      </div>
    );
  };
  const DietaryTable = () => {
    return (
      <div className="py-4">
        {station?.is_parent_station !== true ? (
          <ul className="lists flex flex-wrap gap-2 grid-cols-8 sm:grid-cols-6 md:grid-cols-8 1xl:grid-cols-6 max-w-[70%]">
            {editContent?.getAllDietary?.data?.map((dietary, index) => (
              <li
                key={index}
                onClick={() => handleDietary(dietary?.id)}
                className={`list cursor-pointer text-center text-xs px-3 py-1 rounded-[24px] border ${
                  selectedDietaryItems?.some((item) => item == dietary?.id)
                    ? "bg-primary text-white"
                    : null
                } border-primary col-span-4 sm:col-span-2 md:col-span-1 lg:col-span-2 1xl:col-span-1 font-normal`}
              >
                {dietary?.name}
              </li>
            ))}
          </ul>
        ) : null}
        {selectedDietaryItems?.length >= MAXDIETARYLIST && (
          <div className="text-red-500 text-sm text-right">
            {`*Maximum ${MAXDIETARYLIST} kostpræferencer are allowed.`}
          </div>
        )}
      </div>
    );
  };
  const ImageHandle = (e) => {
    setPreventDoubleImage(true);
    const img = e?.target?.files[0];
    const reader = new FileReader();
    reader.readAsDataURL(img);
    let base64 = "";
    reader.onload = async () => {
      base64 = reader.result;
      setBgImage({ success: "Image is processing wait for few seconds." });
      img?.size > MAX_SIZE
        ? setBgImage({ err: "Uploaded image file size should not exceeds 5MB" })
        : await removeHandle({ img: reader.result, type: "base64" });
      setPreventDoubleImage(false);
    };
    e.target.value = null;
  };
  const removeHandle = async ({ img }) => {
    var data = new FormData();
    data.append("image", img);
    img &&
      (await fetch(`${EXPRESS_BASE_URL}/crop`, {
        method: "POST",
        body: data,
      })
        .then((response) => {
          if (!response.ok) {
            return response.json();
          }
          return response.blob();
        })
        .then((res) => {
          if (res?.error == true) {
            setBgImage({
              err:
                res?.code == "unknown_foreground"
                  ? "The captured image doesn't have a valid object after the image background removal process. Kindly retry with a different object."
                  : res?.code == "insufficient_credits"
                  ? "Insufficient credits to remove the background for the captured Image. Please contact the system administrator for more details."
                  : "Something went wrong. Please try again later",
            });
            return "";
          }

          const reader = new FileReader();
          reader.onloadend = () => {
            setBgRemovedImage({ img: reader.result, type: "base64" });
            setBgImage("");
            return reader.result;
          };
          reader.readAsDataURL(res);
        })
        .catch((e) => {
          console.log(e, "error");
          setBgImage({
            err: "Something went wrong, try again later",
          });
        }));
  };
  const ImageRefreshCall = async () => {
    await directusLoaderLink
      .items("screen_display")
      .readByQuery({
        fields: [
          "*",
          "app_resultant_image.*",
          "recipe_id.app_resultant_image.*",
        ],
        filter: {
          date: { _eq: editContent?.date },
          location_id: { _eq: editContent?.locationId },
          station_name: { station_unique_id: { _eq: stationId?.uniqueId } },
        },
      })
      .then((e) => {
        e?.data[0]?.app_resultant_image?.id
          ? setBgRemovedImage({
              img: e?.data[0]?.app_resultant_image?.id,
              type: "id",
              update: true,
            })
          : setBgImageRefreshError(
              "No updated image found in the station for the day."
            );
      })
      .catch((e) => console.log(e));
  };
  const bannerTable = () => {
    return (
      <div className="py-4">
        <div className="upload-section !bg-[#E3E3AD33]">
          <div className="text-center absolute">
            {bgRemovedImage?.img ? (
              <img
                src={
                  bgRemovedImage?.type == "id"
                    ? getAssetURL(bgRemovedImage?.img)
                    : bgRemovedImage?.img
                }
                alt="uploaded"
                className="w-[132px] h-[88px] z-20 object-contain"
              />
            ) : (
              <i className="ri-add-fill color-primary mr-0"></i>
            )}
          </div>
        </div>
        {bgImage?.err && (
          <span className="text-red-600 flex text-sm">{bgImage?.err}</span>
        )}
        {bgImageRefreshError?.length > 0 && (
          <span className="text-red-600 flex text-sm">
            {bgImageRefreshError}
          </span>
        )}
        {bgImage?.success && (
          <span className="text-primary flex text-sm">{bgImage?.success}</span>
        )}
        <div className="w-full text-primary text-xs mt-1">
          <label htmlFor="files" className="btn cursor-pointer text-primary">
            <i className="ri-add-fill color-primary mr-2"></i>
            Upload Image
          </label>
          <input
            id="files"
            type="file"
            style={{ display: "none", visibility: "hidden" }}
            accept="image/*"
            className="mt-1 border bg-white border-gray-300 px-4 py-1.5 h-9 flex text-black text-xs font-bold justify-center items-center w-full focus:border-primary focus:outline-none focus:ring-1 focus:ring-primary"
            onChange={(e) => ImageHandle(e)}
          />
        </div>
        {/* <div className="w-full">
          <label htmlFor="files1" className="btn cursor-pointer text-primary">
            <i className="ri-add-fill color-primary mr-2"></i>
            Add Image from app
          </label>
          <input
            id="files"
            type="file"
            style={{ display: "none", visibility: "hidden" }}
            accept="image/*"
            className="mt-1 border bg-white border-gray-300 px-4 py-1.5 h-9 flex text-black text-xs font-bold justify-center items-center w-full focus:border-primary focus:outline-none focus:ring-1 focus:ring-primary"
            // onChange={(e)=>sizeCheck(e,3)}
          /> 
        </div>*/}
        <div
          className="cursor-pointer text-primary text-xs mt-2"
          onClick={(e) => setBannerModal(!bannerModal)}
        >
          <i className="ri-eye-line"></i> Preview
        </div>
        <div
          className="cursor-pointer text-primary text-xs mt-2 underline"
          onClick={() => ImageRefreshCall()}
        >
          {/* <i className="ri-eye-line"></i> */}
          Get updated image
        </div>
      </div>
    );
  };
  const isPresent = async ({
    menuInfo,
    translatedMenuInfo,
    allergensList,
    dietaryList,
    ingredientList,
    imageId,
    co2,
    calorie,
  }) => {
    const filterValue = {
      menu_info: { _eq: menuInfo?.trim() },
      is_global: { _eq: true },
    };
    const resId =
      menuInfo?.trim()?.length > 0 ||
      translatedMenuInfo?.trim()?.length > 0 ||
      allergensList?.length > 0 ||
      dietaryList?.length > 0 ||
      ingredientList?.length > 0
        ? await directusLoaderLink
            .items("recipe_book")
            .readByQuery({
              fields: [
                "*",
                "allergens_list.id",
                "allergens_list.Allergens_id.*",
                "dietary_list.id",
                "dietary_list.Dietary_id.*",
                "ingredients_list.*",
                "ingredients_list.new_climate_db.*",
              ],
              filter: filterValue,
              sort: ["id"],
            })
            .then(async (res) => {
              const response = [];
              res?.data?.map((e) => {
                const menuSame =
                  e?.menu_info?.trim() === menuInfo?.trim() &&
                  e?.menu_info_1?.trim() === translatedMenuInfo?.trim();
                const allergenSame =
                  JSON?.stringify(
                    e?.allergens_list?.length > 0
                      ? e?.allergens_list?.map((l) => l?.Allergens_id?.id)
                      : -1
                  ) ===
                  JSON.stringify(
                    allergensList?.length > 0 ? allergensList : -1
                  );

                // const dietarySame =
                //   JSON.stringify(
                //     e?.dietary_list?.length > 0
                //       ? e?.dietary_list?.map((l) => l?.Dietary_id?.id)
                //       : -1
                //   ) ===
                //   JSON.stringify(dietaryList?.length > 0 ? dietaryList : -1);

                // const ingredientsSame =
                //   JSON.stringify(
                //     e?.ingredients_list?.length > 0
                //       ? e?.ingredients_list?.map((l) => l?.new_climate_db?.id)
                //       : -1
                //   ) ===
                //   JSON.stringify(
                //     ingredientList?.length > 0
                //       ? ingredientList?.map((l) => l?.Item1?.id)
                //       : -1
                //   );
                // const ingredientsUnitsSame =
                //   JSON.stringify(
                //     e?.ingredients_list?.length > 0
                //       ? e?.ingredients_list?.map((l) => l?.unit)
                //       : -1
                //   ) ===
                //   JSON.stringify(
                //     ingredientList?.length > 0
                //       ? ingredientList?.map((l) => l?.Value2)
                //       : -1
                //   );
                response?.push(
                  menuSame
                    ? // &&allergenSame &&
                      //   dietarySame &&
                      //   ingredientsSame &&
                      //   ingredientsUnitsSame
                      "true"
                    : "false"
                );
                return "";
              });
              const index = response?.includes("true")
                ? response?.findIndex((e) => e == "true")
                : -1;
              index !== -1 &&
                saveType !== "day" &&
                // ingredientList?.length > 0 &&
                (await IngredientUpdate({
                  id: res?.data[index]?.id,
                  values: ingredientList,
                }));
              // for global save allergens and dietary re update
              index !== -1 &&
                menuId?.recipeId !== res?.data[index]?.id &&
                (await recipeUpdate({
                  menuInfo: menuInfo,
                  translatedMenuInfo: translatedMenuInfo,
                  Id: res?.data[index]?.id,
                  allergensList: allergensList,
                  dietaryList: dietaryList,
                  imageId: imageId,
                  co2: co2,
                  calorie: calorie,
                }));
              return index !== -1 ? res?.data[index]?.id : -1;
            })
            .catch((err) => {
              console.log(err);
            })
        : -1;
    return resId;
  };
  const recipeUpdate = async ({
    Id,
    allergensList,
    dietaryList,
    imageId,
    co2,
    calorie,
  }) => {
    const body = {
      menu_info: menuInfo?.trim(),
      menu_info_1: translatedMenuInfo?.trim(),
      allergens_list: allergensList
        ?.filter((x) => x != undefined)
        ?.map((al) => ({ Allergens_id: { id: al } })),
      dietary_list: dietaryList
        ?.filter((x) => x != undefined)
        ?.map((al) => ({ Dietary_id: { id: al } })),
      is_global: saveType === "day" ? false : true,
      total_co2: co2,
      total_calories: calorie,
    };
    await directusLoaderLink
      .items("recipe_book")
      ?.updateOne(
        Id,
        imageId === null ? body : { ...body, app_resultant_image: imageId }
      )
      .then((re) => re?.id);
  };
  const recipeCreate = async ({
    menuInfo,
    translatedMenuInfo,
    allergensList,
    dietaryList,
    ingredientList,
    calorie,
    co2,
    imageId,
  }) => {
    const recipeIsPresent =
      saveType === "day"
        ? -1
        : await isPresent({
            menuInfo,
            translatedMenuInfo,
            allergensList,
            dietaryList,
            ingredientList,
            imageId,
            co2,
            calorie,
          });
    if (recipeIsPresent !== -1) {
      return recipeIsPresent;
    } else {
      const body = {
        menu_info: menuInfo?.trim(),
        menu_info_1: translatedMenuInfo?.trim(),
        allergens_list: allergensList
          ?.filter((x) => x != undefined)
          ?.map((al) => ({ Allergens_id: { id: al } })),
        dietary_list: dietaryList
          ?.filter((x) => x != undefined)
          ?.map((al) => ({ Dietary_id: { id: al } })),
        total_co2: co2,
        total_calories: calorie,
        is_global: saveType === "day" ? false : true,
      };
      const recipeId = await directusLoaderLink
        .items("recipe_book")
        ?.createOne(
          imageId === null ? body : { ...body, app_resultant_image: imageId }
        )
        .then((re) => re?.id)
        .catch((e) => {
          console.log(e, "error");
        });
      await IngredientUpdate({
        id: recipeId,
        values: ingredientList,
        create: true,
      });
      return recipeId;
    }
  };
  const createHandle = async ({
    menuInfo,
    translatedMenuInfo,
    allergensList,
    dietaryList,
    ingredientList,
    calorie,
    co2,
    imageId,
    portionsProduced,
    portionsSold
  }) => {
    const recipeId = await recipeCreate({
      menuInfo,
      translatedMenuInfo,
      allergensList,
      dietaryList,
      ingredientList,
      calorie,
      co2,
      imageId,
    });
    if (recipeId > 0) {
      await directusLoaderLink
        .items("screen_display")
        ?.createOne({
          date: editContent?.date,
          location_id: editContent?.currentUser?.selected_locations?.id,
          station_name: stationId?.stationId,
          recipe_id: recipeId,
          portions_produced: portionsProduced,
          portions_sold: portionsSold
        })
        .then((re) => {
          localStorage.setItem(
            "MenuSave",
            `${saveType === "day" ? "create" : "edit"}`
          );
          location.reload();
        })
        .catch((e) => {
          console.log(e, "error");
        });
    }
  };
  const recipeCount = async (prop) => {
    const count = await directusLoaderLink
      .items("screen_display")
      .readByQuery({
        fields: ["*"],
        filter: { recipe_id: { id: { _eq: prop } } },
        meta: ["filter_count"],
      })
      .then((res) => {
        return res?.meta?.filter_count > 1 ? true : false;
      });
    return count;
  };
  const updateHandle = async ({
    menuInfo,
    translatedMenuInfo,
    allergensList,
    dietaryList,
    ingredientList,
    calorie,
    co2,
    imageId,
    portionsProduced,
    portionsSold
  }) => {
    if (saveType === "day") {
      const recipePresentCount = menuId?.recipeId
        ? await recipeCount(menuId?.recipeId)
        : true;
      if (recipePresentCount) {
        const recipeId = await recipeCreate({
          menuInfo,
          translatedMenuInfo,
          allergensList,
          dietaryList,
          ingredientList,
          calorie,
          co2,
          imageId,
        });
        if (recipeId > 0) {
          const body = { recipe_id: recipeId,
            portions_produced: portionsProduced,
            portions_sold: portionsSold };
          await ScreenDisplayUpdate({
            Ids: [menuId?.menu],
            body:
              imageId === null
                ? body
                : { ...body, app_resultant_image: imageId },
          });
        }
      } else {
        await IngredientUpdate({
          id: menuId?.recipeId,
          values: ingredientList,
        });
        await recipeUpdate({
          menuInfo: menuInfo,
          translatedMenuInfo: translatedMenuInfo,
          Id: menuId?.recipeId,
          allergensList: allergensList,
          dietaryList: dietaryList,
          imageId: imageId,
          co2: co2,
          calorie: calorie,
        });
        if (imageId !== null &&
          bgImg?.img !== imageId){
            await ScreenDisplayUpdate({
            Ids: [station?.id],
            body: { app_resultant_image: imageId,  
                    portions_produced: portionsProduced,
                    portions_sold: portionsSold },
          });
        }
        else{
          await ScreenDisplayUpdate({
            Ids: [station?.id],
            body: { portions_produced: portionsProduced,
                    portions_sold: portionsSold },
          });
        }
      }
    } else {
      const recipeIsPresent = await isPresent({
        menuInfo,
        translatedMenuInfo,
        allergensList,
        dietaryList,
        ingredientList,
        imageId,
        co2,
        calorie,
      });
      if (recipeIsPresent !== -1 && menuId?.recipeId !== recipeIsPresent) {
        if (menuId?.recipeId === undefined) {
          await ScreenDisplayUpdate({
            Ids: [menuId?.menu],
            body: { recipe_id: recipeIsPresent,
                    portions_produced: portionsProduced,
                    portions_sold: portionsSold 
            },
          });
        } else {
          const screenUpdateIds = await directusLoaderLink
            .items("screen_display")
            .readByQuery({
              fields: ["id"],
              filter: {
                location_id: editContent?.currentUser?.selected_locations?.id,
                recipe_id: { _eq: menuId?.recipeId },
              },
            })
            .then((e) => {
              return e?.data?.map((e) => e?.id);
            })
            .catch((e) => console.log(e));
          screenUpdateIds?.length > 0 &&
            (await ScreenDisplayUpdate({
              Ids: screenUpdateIds,
              body: { recipe_id: recipeIsPresent,
                  portions_produced: portionsProduced,
                 portions_sold: portionsSold },
            }));
          imageId !== null &&
            bgImg?.img !== imageId &&
            (await ScreenDisplayUpdate({
              Ids: [station?.id],
              body: { app_resultant_image: imageId,
                portions_produced: portionsProduced,
                portions_sold: portionsSold },
            }));
        }
      } else {
        const body = {
          menu_info: menuInfo,
          menu_info_1: translatedMenuInfo,
          allergens_list: allergensList
            ?.filter((x) => x != undefined)
            ?.map((al) => ({ Allergens_id: { id: al } })),
          dietary_list: dietaryList
            ?.filter((x) => x != undefined)
            ?.map((al) => ({ Dietary_id: { id: al } })),
          total_co2: co2,
          total_calories: calorie,
          is_global: true,
        };
        await directusLoaderLink
          .items("recipe_book")
          ?.updateOne(
            menuId?.recipeId,
            imageId === null ? body : { ...body, app_resultant_image: imageId }
          )
          .then((re) => re?.id)
          .catch((e) => {
            console.log(e, "error");
          });
        await IngredientUpdate({
          id: menuId?.recipeId,
          values: ingredientList,
        });
        if(imageId !== null && bgImg?.img !== imageId){
            await ScreenDisplayUpdate({
            Ids: [station?.id],
            body: { app_resultant_image: imageId, portions_produced: portionsProduced, portions_sold: portionsSold }});
        }
        else{
          await ScreenDisplayUpdate({
            Ids: [station?.id],
            body: { portions_produced: portionsProduced, portions_sold: portionsSold }});
        }
      }
    }
    localStorage.setItem(
      "MenuSave",
      `${saveType === "day" ? "create" : "edit"}`
    );
    location.reload();
  };
  const ScreenDisplayUpdate = async ({ Ids, body }) => {
    await directusLoaderLink
      .items("screen_display")
      ?.updateMany(Ids, body)
      .then((re) => {})
      .catch((e) => {
        console.log(e);
      });
  };
  const saveHandle = async (prop) => {
    setPreventDouble(true);
    if (!ingredient?.map((e) => e?.name?.trim()?.length > 0)?.includes(false)) {
      if (
        !ingredient
          ?.map((e) => e?.Value2?.trim()?.length > 0)
          ?.includes(false) &&
        !ingredient?.map((e) => Number(e?.Value2?.trim()) > 0)?.includes(false)
      ) {
        const newList = [];
        ingredient?.map((e) =>
          newList.push({
            IndexId: e?.IndexId,
            Item1: e?.Item1,
            name: e?.Item1?.name,
            Value2: e?.Value2,
            item: e?.item,
            prev1: e?.Item1,
            prev2: e?.Value2,
          })
        );
        const token = await AuthToken();
        const imageId =
          bgRemovedImage?.img !== undefined
            ? bgRemovedImage?.update !== true
              ? await ImageUploadToDirectus({
                  token: token,
                  img: bgRemovedImage?.img,
                })
              : { id: bgRemovedImage?.img }
            : null;
        if (menuId?.menu !== undefined) {
          await updateHandle({
            menuInfo: menuInfo,
            translatedMenuInfo: translatedMenuInfo,
            allergensList: selectedMenuItems,
            dietaryList: selectedDietaryItems,
            ingredientList: newList,
            calorie: totalCalories,
            co2: totalCO2,
            imageId: imageId?.id,
            portionsProduced: portionsProduced,
            portionsSold: portionsSold
          });
        } else {
          await createHandle({
            menuInfo: menuInfo,
            translatedMenuInfo: translatedMenuInfo,
            allergensList: selectedMenuItems,
            dietaryList: selectedDietaryItems,
            ingredientList: newList,
            calorie: totalCalories,
            co2: totalCO2,
            imageId: imageId?.id,
            portionsProduced: portionsProduced,
            portionsSold: portionsSold
          });
        }
      } else {
        toast.error("Please enter a valid ingredient usage (in g).");
        setPreventDouble(false);
      }
    } else {
      toast.error("Please select a valid ingredient name");
      setPreventDouble(false);
    }
  };
  const ImageUploadToDirectus = async ({ token, img }) => {
    var requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        apiURL: `${URL}/files`,
        apiToken: token,
        base64data: `${img}`,
      }),
    };
    const imageId = await fetch(
      `${EXPRESS_BASE_URL}/image-convert`,
      requestOptions
    )
      .then((response) => response.json())
      .then((result) => {
        return result;
      })
      .catch((error) => {
        console.log("error", error);
        return error;
      });
    return imageId?.id ? imageId : null;
  };
  const AuthToken = async () => {
    const token = await fetch(`${URL}/auth/login`, {
      method: "POST",
      body: JSON.stringify({
        email: user?.email,
        password: user?.password,
      }),
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
    })
      .then((response) => response.json())
      .then(async (e) => {
        return e?.data?.access_token;
      });
    return token;
  };
  const IngredientUpdate = async (prop) => {
    const values = prop?.values;
    var deleteData = [];
    await directusLoaderLink
      .items("ingredients_and_units")
      .readByQuery({
        fields: ["*.*.*"],
        filter: {
          recipe_id: { _eq: prop?.id },
        },
      })
      .then(async (res) => {
        var deleteData =
          res?.data?.length > 0 &&
          res?.data
            ?.filter(
              (e) =>
                !values?.find((ingredient) => {
                  return ingredient?.item?.id === e?.id;
                })
            )
            ?.map((res) => res);

        const deleteIds =
          deleteData?.length > 0 && deleteData?.map((e) => e?.id);

        deleteIds?.length > 0 &&
          (await directusLoaderLink
            .items("ingredients_and_units")
            .deleteMany(deleteIds));
      })
      .catch((e) => {
        console.log(e);
      });
    const missingValue = values?.filter(
      (e) => !deleteData?.find((a) => e?.Item1?.id === a?.new_climate_db?.id)
    );
    missingValue?.length > 0 &&
      missingValue?.map(async (e) => {
        const [screenId, climateId, unit] = [prop?.id, e?.Item1?.id, e?.Value2];
        if (e?.item === undefined || prop?.create === true) {
          await directusLoaderLink
            .items("ingredients_and_units")
            ?.createOne({
              new_climate_db: climateId,
              recipe_id: screenId,
              unit: unit,
            })
            .then((re) => {})
            .catch((e) => {
              console.log(e, "error");
            });
        } else {
          await directusLoaderLink
            .items("ingredients_and_units")
            .updateOne(e?.item?.id, {
              new_climate_db: climateId,
              unit: unit,
            })
            .then((re) => {})
            .catch((e) => {
              console.log(e);
            });
        }
      });
  };

  const allergensLists = editContent?.getAllAllergens?.data
    ?.filter((allergen) => selectedMenuItems?.some((e) => e == allergen?.id))
    ?.map((e) => {
      return { Allergens_id: e };
    });
  const dietaryLists = editContent?.getAllDietary?.data
    ?.filter((dietary) => selectedDietaryItems?.some((e) => e == dietary?.id))
    ?.map((e) => {
      return { Dietary_id: e };
    });
  const titleCheck = parentStationName?.station_name_translation?.filter(
    (e) => e?.location_id == editContent?.currentUser?.selected_locations?.id
  );
  const ParentStationName =
    titleCheck?.[0]?.name?.length > 0
      ? titleCheck?.[0]?.name
      : parentStationName?.station_name;
  return (
    <div className="w-100,h-100">
      <Modal
        isOpen={modalIsOpen}
        style={customStyles}
        ariaHideApp={false}
        contentLabel="Banner Preview"
      >
        <div className="px-12 py-12 w-full relative lg:px-9 station-preview bg-[#F8F8F8]">
          <div className="font-normal text-primary flex flex-col text-center pt-10">
            <span className="font-bold w-full">Build A Recipe</span>
            <span className="font-normal w-full uppercase">
              {ParentStationName?.length > 0
                ? parentStationName?.station_name + " - "
                : ""}
              {stationName}
            </span>
          </div>
          <div className="ml-10 font-normal text-primary" onClick={closeModal}>
            <span className="left-0 font-normal text-sm cursor-pointer">
              <i className="ri-arrow-left-line mr-1 relative top-[2px]"></i>{" "}
              Back to Menu of The day
            </span>
          </div>
          <div className="p-10">
            {/* <div className="flex justify-end">
              <button
                className="future w-10 h-10 min-w-[100px] min-h-[40px] inline-flex items-center justify-center border border-transparent bg-primary text-sm font-medium text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 sm:w-auto disabled opacity-50"
                disabled
              >
                Ask AI
              </button>
            </div> */}
            <div className="editmenuitem pb-[20px] mt-5 max-w-[70%]">
              <div className="card w-full flex flex-row gap-5">
                <div className="card col-span-12 md:col-span-12 lg:col-span-4 mb-4 w-full">
                  <div className="mb-2">{DkTitle()}</div>
                  {station?.station_name?.is_parent_station !== true ? (
                    <div
                      className={`border bg-white relative border-${
                        menuInfo?.length <= MAX_LIMIT ? "gray" : "red"
                      }-300 border-l-8 border-l-[#B6D989] px-3 pl-2 py-2`}
                      style={{
                        borderLeftColor:
                          stationColor[indexValue] ??
                          stationColor?.slice(-1)?.[0],
                        borderLeftWidth: "6px",
                      }}
                    >
                      <CustomInput
                        lang={true}
                        value={menuInfo}
                        setValue={setMenuInfo}
                        valueEng={translatedMenuInfo}
                        setValueEng={setTranslatedMenuInfo}
                        setAllergens={setSelectedMenuItems}
                        setDietary={setSelectedDietaryItems}
                        setIngredient={setIngredient}
                        setBgRemovedImage={setBgRemovedImage}
                      />

                      <span className="block text-xs text-[#AAA8A8] font-medium">
                        {menuInfo?.length}/{MAX_LIMIT}
                        characters
                      </span>
                    </div>
                  ) : null}
                </div>

                <div className="card col-span-12 md:col-span-12 lg:col-span-4 mb-4 w-full">
                  <div className="mb-2">{EnTitle()}</div>
                  {station?.station_name?.is_parent_station !== true ? (
                    <div
                      className={`border bg-white relative border-${
                        translatedMenuInfo?.length <= MAX_LIMIT ? "gray" : "red"
                      }-300 border-l-8 border-l-[#B6D989] px-3 pl-2 py-2`}
                      style={{
                        borderLeftColor:
                          stationColor[indexValue] ??
                          stationColor?.slice(-1)?.[0],
                        borderLeftWidth: "6px",
                      }}
                    >
                      <CustomInput
                        lang={false}
                        value={translatedMenuInfo}
                        setValue={setTranslatedMenuInfo}
                        valueEng={menuInfo}
                        setValueEng={setMenuInfo}
                        setAllergens={setSelectedMenuItems}
                        setDietary={setSelectedDietaryItems}
                        setIngredient={setIngredient}
                        setBgRemovedImage={setBgRemovedImage}
                      />
                      <span className="block text-xs text-[#AAA8A8] font-medium">
                        {translatedMenuInfo?.length}/{MAX_LIMIT} characters
                      </span>
                    </div>
                  ) : null}
                </div>
              </div>
            </div>
            <div className="portions-tabs flex flex-row max-w-[70%]">
            <div className="w-full flex flex-row gap-5">
              <div className="w-[50%]">
                <div className="w-full flex flex-row items-center">
                  <label htmlFor="portions_produced" className="mr-3">Portions Produced</label>
                  <input
                    name="portions_produced"
                    type="number"
                    onKeyDown={ (evt) => evt.key === 'e' && evt.preventDefault() }
                    placeholder="0"
                    value={portionsProduced}
                    onChange={(e) =>
                      setPortionsProduced(e?.target?.value)
                    }
                    onWheel={(e) => e?.target?.blur()}
                    className="border border-[#777] border-collapse rounded-md justify-end  my-1 block w-1/2 max-w-[100px] py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium text-black sm:text-sm"
                  />
                </div>
              </div>
              <div className="w-[50%]">
                <div className="w-full flex flex-row items-center">
                  <label htmlFor="portions_sold" className="mr-3">Portions Sold</label>
                  <input
                    name="protions_sold"
                    type="number"
                    onKeyDown={ (evt) => evt.key === 'e' && evt.preventDefault() }
                    placeholder="0"
                    value={portionsSold}
                    onChange={(e) =>
                      setPortionsSold(e?.target?.value)
                    }
                    onWheel={(e) => e?.target?.blur()}
                    className="border border-[#777] border-collapse rounded-md justify-end  my-1 block w-1/2 max-w-[100px] py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium text-black sm:text-sm"
                  />

                </div>
              </div>
            </div>
            </div>
            <div className="ingredients-tabs pt-8 ">
              <div className="tabs">
                <ul className="inline-flex tabs-section">
                  {[
                    "Ingredienslist",
                    "Allergener",
                    "Kostpræferencer",
                    "Billede",
                  ]?.map((e, i) => (
                    <li
                      key={i}
                      onClick={() => showTable(e)}
                      className={`${
                        table == e && "active"
                      } font-normal text-sm`}
                    >
                      <span>{e}</span>
                    </li>
                  ))}
                </ul>
              </div>
              <div className="tabs-section-content px-5 pt-[15px] pb-[25px] bg-white border border-[#C5C5C5]">
                {table == "Ingredienslist"
                  ? IngredientsTable()
                  : table == "Allergener"
                  ? AllergensTable()
                  : table == "Kostpræferencer"
                  ? DietaryTable()
                  : bannerTable()}
              </div>
            </div>
            <div className="flex flex-col justify-end mt-5">
              <div className="flex justify-end text-black text-sm items-center mb-2">
                <input
                  type="checkbox"
                  value="global"
                  checked={saveType === "global" ? true : false}
                  onChange={() => setSaveType("global")}
                  className="mr-2 w-4 h-4 text-primary border-gray-300 rounded focus:ring-primary focus:ring-2"
                />
                Apply changes to
                <p>
                  <span className="font-bold ml-1">global recipe </span>
                </p>
              </div>
              <div className="flex justify-end text-black text-sm items-center">
                <input
                  type="checkbox"
                  value="day"
                  checked={saveType === "day" ? true : false}
                  onChange={() => setSaveType("day")}
                  className="mr-2 w-4 h-4 text-primary border-gray-300 rounded focus:ring-primary focus:ring-2"
                />
                Apply changes to{" "}
                <p>
                  <span className="font-bold ml-1">this day only </span>
                </p>
              </div>
            </div>
            <div className="flex justify-end mt-5">
              <button
                onClick={() =>
                  preventDouble || preventDoubleImage ? null : saveHandle()
                }
                disabled={preventDouble}
                className="min-w-[176px] min-h-[30px] inline-flex items-center justify-center border border-transparent bg-primary text-sm font-medium text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 sm:w-auto"
              >
                {preventDouble ? "Saving..." : "Save"}
              </button>
            </div>
          </div>
        </div>
      </Modal>
      {bannerModal && (
        <BannerPopup
          modalIsOpenParent={bannerModal}
          setIsOpenParent={setBannerModal}
          data={{
            menuInfo: menuInfo,
            menuInfo1: translatedMenuInfo,
            allergens: allergensLists,
            dietary: dietaryLists,
            stationName: stationName,
            stationImage: station?.station_name?.station_image_1?.id,
            plateImg:
              bgRemovedImage?.img !== undefined
                ? {
                    id: bgRemovedImage?.img,
                    showType:
                      bgRemovedImage?.img?.length < 50 ? "id" : "base64",
                  }
                : station?.app_resultant_image,
            RecipePlateImg: station?.recipe_id?.app_resultant_image?.id,
            stationId: stationId?.stationId,
            locationId: editContent?.currentUser?.selected_locations?.id,
            stationUniqueId: stationId?.uniqueId,
          }}
          calorie={totalCalories}
          co2={totalCO2}
          isAdminUser={false}
        />
      )}
    </div>
  );
});

export default EditMenuModel;
