import React, { useState } from "react";
import "react-autocomplete-input/dist/bundle.css";
import { directusLoaderLink } from "../../utils/constants";
import Select from "react-select";
import Highlighter from "react-highlight-words";
function Todoingredients({ ingredient, setIngredient, setIngredientChanged}) {
  const [searchMenu, setSearchMenu] = useState([]);
  const [searchText, setSearchText] = useState("")

   const searchMenuRender = async ({value}) => {
    const filterValue = value?.length > 0 ? { name: { _icontains: value } } : { name: { _nempty: null } };
    await directusLoaderLink
      .items("new_ingredients_data")
      .readByQuery({
        fields: ['*'],
        filter: filterValue,
        sort: ["-date_updated", "id"],
        limit: 150
      })
      .then((res) => {
        setSearchMenu(res?.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  React.useEffect(() => {
    searchMenuRender({value: "" });
  }, []);

  React.useEffect(() => {
    const id = setTimeout(() => {
      searchMenuRender({value: searchText});
    }, 700)
    return () => {
      clearTimeout(id)
    }
  }, [searchText]);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value, Item } = e;
    const list = [...ingredient];
    setIngredientChanged(true);
    list[index][name] = name == "name" ? e?.value?.name : value;
    list[index]["IndexId"] = index;
    name == "name" && (list[index]["Item1"] = value);
    setIngredient(list);
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    setIngredientChanged(true);
    setIngredient((oldValues) => {
      return oldValues?.filter((_, i) => i !== index);
    });
  };

  const styles1 = { 
    menuList: (base) => ({
      ...base,
      height: "110px",

      "::-webkit-scrollbar": {
        width: "6px",
      },
      "::-webkit-scrollbar-track": {
        background: "#2684FF",
      },
      "::-webkit-scrollbar-thumb": {
        background: "#888",
      },
      "::-webkit-scrollbar-thumb:hover": {
        background: "#555",
      },
    }),
    control: (base, state) => ({
      ...base,
      border: "none",
      marginTop: "4px",
      borderColor: state.isFocused ? "grey" : "red",
    }),
  };

  function formatOptionLabel({ name }, { inputValue }) {
    return (
      <Highlighter
        searchWords={[inputValue]}
        textToHighlight={name}
        highlightClassName="select-ingredient"
      />
    );
  }

  return (
    <div>
      {ingredient?.map((x, i) => {
        return (
          <div
            className="pr-4 pl-0 data-list popauto my-2 flex flex-row justify-between"
            key={i}
          >
            <Select
              id="Ingredient"
              classNamePrefix="cursor-pointer react-select"
              className=" react-select-container  border-gray-500 border-0 text-gray-900 text-sm w-1/3 min-h-[42px] flex"
              defaultMenuIsOpen={false}
              maxMenuHeight={250}
              onChange={(e) =>
                handleInputChange({ value: e, name: "name", item: {} }, i)
              }
              components={{
                IndicatorSeparator: () => null
              }}
              hideSelectedOptions
              cacheOptions={false}
              value={x?.name?.length>0?x:""}
              formatOptionLabel={formatOptionLabel}
              options={searchMenu?.map((e) => e)}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              onInputChange={(e)=> setSearchText(e)}
              placeholder={"Søg efter ingrediens"}
              styles={{
                styles1,
                control: (baseStyles) => ({
                  ...baseStyles,
                  border: "none",
                  boxShadow: "none",
                }),
              }}
            />
            <div className="justify-end flex flex-row ">
              <input
                name="Value2"
                type="number"
                onKeyDown={ (evt) => evt.key === 'e' && evt.preventDefault() }
                placeholder="0"
                value={x?.Value2}
                onChange={(e) =>
                  handleInputChange( { value: e?.target?.value, name: "Value2" }, i)
                   }
                onWheel={(e) => e?.target?.blur()}
                className="border border-[#777] border-collapse rounded-l-md justify-end  my-1 block w-1/2 py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium text-black sm:text-sm"
              />
              <span className="unit-section text-center border border-black rounded-r-md my-1 p-2 border-collapse	">
                g
              </span>
            </div>
            <div className="justify-end items-end flex-row m-1">
              <button
                type="button"
                title="Delete Ingredient"
                className="w-10 h-10 min-w-[40px] min-h-[40px] inline-flex items-center justify-center rounded-full border border-transparent text-sm font-medium text-black shadow-sm focus:outline-none focus:ring-offset-2 sm:w-auto"
                onClick={() => handleRemoveClick(i)}
              >
                <span>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="feather feather-trash w-5 h-5"
                  >
                    <polyline points="3 6 5 6 21 6"></polyline>
                    <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                  </svg>
                </span>
              </button>
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default Todoingredients;
